<template>
  <div v-if="showModal">
    <Modal @close="toggleModal" />
  </div>
  <link rel="preconnect" href="https://fonts.googleapis.com" />
  <link rel="preconnect" href="https://fonts.gstatic.com" crossorigin />
  <link
    href="https://fonts.googleapis.com/css2?family=Noto+Serif:ital,wght@0,100..900;1,100..900&family=Rubik:ital,wght@0,300..900;1,300..900&display=swap"
    rel="stylesheet"
  />
  <form @submit.prevent="handleSubmit">
    <div class="header">
      <h1>MGM</h1>
      <div class="header2">
        <h2>Global</h2>
        <h2>Distribution</h2>
      </div>
    </div>
    <h2 class="header-signin">Sign in</h2>
    <label>Email</label>
    <input
      type="email"
      required
      placeholder="Enter your email address (e.g. xyz@gmail.com)"
      v-model="email"
    />

    <label>Password</label>
    <div class="pass-and-showbtn">
      <input
        :type="passwordFieldType"
        required
        placeholder="Enter your Password (Minimum 6 characters long)"
        v-model="password"
      />
      <button @click="switchVisibility" class="show-hide-btn">
        {{ title }}
      </button>
    </div>
    <div v-if="passwordError" class="error">{{ passwordError }}</div>
    <!-- <input :type="passwordFieldType" v-model="password" /> -->

    <a href="#" class="forgot-pass" @click="toggleModal">Forgot Password?</a>
    <div class="keep-signedin">
      <input
        type="checkbox"
        class="check-box"
        required
        v-model="keepSignedIn"
      />
      <label class="signin-description">Keep me signed in !</label>
    </div>

    <button class="sign-in-btn" @click="submittedEvent">
      <span v-if="submitted">LOGGED IN!</span>
      <span v-else> SIGN IN</span>
    </button>

    <div class="footer-terms">
      <p class="terms-heading">Read our Terms of Service</p>
      <a href="#" class="icon">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          stroke-width="1.5"
          stroke="currentColor"
          class="terms-icon"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            d="M13.5 6H5.25A2.25 2.25 0 0 0 3 8.25v10.5A2.25 2.25 0 0 0 5.25 21h10.5A2.25 2.25 0 0 0 18 18.75V10.5m-10.5 6L21 3m0 0h-5.25M21 3v5.25"
          />
        </svg>
      </a>
    </div>
  </form>
  <div class="grey-box"></div>
  <div class="co-partners">
    <p class="co-partners-description">OTHER WAYS TO WATCH</p>
    <div class="co-partners-companies">
      <p class="tv">
        <svg
          xmlns="http://www.w3.org/2000/svg"
          fill="none"
          viewBox="0 0 24 24"
          stroke-width="1.5"
          stroke="currentColor"
          class="tv-icon"
        >
          <path
            stroke-linecap="round"
            stroke-linejoin="round"
            d="M6 20.25h12m-7.5-3v3m3-3v3m-10.125-3h17.25c.621 0 1.125-.504 1.125-1.125V4.875c0-.621-.504-1.125-1.125-1.125H3.375c-.621 0-1.125.504-1.125 1.125v11.25c0 .621.504 1.125 1.125 1.125Z"
          />
        </svg>
        tv
      </p>
      <p class="ios">iOS</p>
      <p class="roku">Roku</p>
      <p class="amazon"><strong>amazon</strong> fire</p>
    </div>
    <p class="co-partners-footer">Search for AppleTV+ FYC on your App Store</p>
  </div>

  <!-- <p>Email: {{ email }}</p>
  <p>Password: {{ password }}</p> -->
</template>

<script>
import Modal from "./Modal.vue";
export default {
  components: { Modal },
  data() {
    return {
      email: "",
      password: "",
      keepSignedIn: false,
      passwordError: "",
      submitted: false,
      passwordFieldType: "password",
      show: true,
      title: "Show",
      showModal: false,
    };
  },
  methods: {
    handleSubmit() {
      //validate password, lets say password length less than 5 characters will throw an error
      this.passwordError =
        this.password.length > 5
          ? ""
          : "Password must be at least 6 characters long";
    },
    submittedEvent() {
      if (
        this.email != "" &&
        this.password != "" &&
        this.keepSignedIn === true &&
        this.password.length > 5
      ) {
        this.submitted = true;
      } else {
        this.submitted = false;
      }
    },
    switchVisibility() {
      this.passwordFieldType =
        this.passwordFieldType === "password" ? "text" : "password";
      this.show = !this.show;
      if (this.show) {
        this.title = "Show";
      } else {
        this.title = "Hide";
      }
    },
    switchShowHide() {
      this.show = !this.show;
    },
    toggleModal() {
      this.showModal = !this.showModal;
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
html {
  font-size: 10px;
}
body {
  background-image: linear-gradient(rgba(0, 0, 0, 0.8), rgba(0, 0, 0, 0.6)),
    url(movie.jpg);
  background-size: cover;
}

form {
  max-width: 42rem;
  margin: 3rem auto;
  background: rgba(0, 0, 0, 0.8);
  text-align: left;
  padding: 4rem;
  border-top-left-radius: 0.4rem;
  border-top-right-radius: 0.4rem;
  margin-bottom: 0;
}
label {
  color: #5d93c4;
  display: inline-block;
  margin: 2.5rem 0 1.5rem;
  font-size: 1.2rem;
  /* text-transform: uppercase; */
  letter-spacing: 0.1rem;
  font-weight: 600;
}
input {
  display: block;
  padding: 1rem 0.6rem;
  width: 100%;
  box-sizing: border-box;
  border: none;
  /* border-bottom: 0.1rem solid #ddd; */
  color: #fff;
  /* border: 0.5px solid black; */
  border-radius: 0.3rem;
  background-color: #212529;
}
.header {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-bottom: 2.4rem;
  color: #e9ecef;
}
.header h1 {
  font-size: 3.4rem;
  margin-right: 1rem;
  font-family: "Noto Serif", serif;
}
.header-signin {
  color: #e9ecef;
}
input[type="checkbox"] {
  display: inline-block;
  width: 1.5rem;
  margin: 0 1rem 0 0;
  position: relative;
  top: 0.2rem;
  margin-bottom: 3.2rem;
}

::placeholder {
  color: rgba(255, 255, 255, 0.4);
}
.forgot-pass:link,
.forgot-pass:visited {
  text-decoration: none;
  display: inline-block;
  margin-top: 1rem;
  width: 100%;
  text-align: end;
  color: #999;
  font-size: 1.2rem;
  letter-spacing: 0.7px;
  font-weight: 570;
}
.forgot-pass:hover,
.forgot-pass:active {
  text-decoration: none;
  display: inline-block;
  margin-top: 1rem;
  width: 100%;
  text-align: end;
  color: orangered;
}
.signin-description {
  color: #999;
  font-weight: 540;
}
.sign-in-btn {
  width: 100%;
  padding: 1rem 2rem;
  border: none;
  box-shadow: 0 0 0.1rem 0;
  background-color: #165a9a;
  color: #fff;
  font-weight: 540;
  letter-spacing: 1.1px;
  border-radius: 0.3rem;
  cursor: pointer;
}
.sign-in-btn:hover {
  background-color: #0e3c67;
  transition-delay: 5ms;
  font-weight: 600;
}
.terms-heading {
  color: #999;
}
.terms-icon {
  width: 1.5rem;
  height: 1.2rem;
  margin-left: 0.2rem;
  color: #fff;
}
.terms-icon:hover {
  color: orangered;
}
.footer-terms {
  display: flex;
  margin-top: 2rem;
}
.footer-terms p {
  font-size: 1.1rem;
}
.grey-box {
  width: 42rem;
  height: 5rem;
  background-color: #343a40;
  margin: 0 auto;
}
.co-partners {
  width: 42rem;
  /* height: 3rem; */
  background-color: #212529;
  margin: 0 auto;
  margin-bottom: 3rem;
  padding: 2rem 4rem;
  border-bottom-left-radius: 0.4rem;
  border-bottom-right-radius: 0.4rem;
}
.co-partners-description {
  font-size: 0.9rem;
  color: #ffffffda;
  font-weight: 600;
  letter-spacing: 1.1px;
}
.co-partners-companies {
  display: flex;
  justify-content: space-evenly;
  padding: 1.2rem 0;
  color: #fff;
  font-size: 1.2rem;
  font-weight: 600;
}
.error {
  color: red;
  font-size: 1rem;
  font-weight: 600;
  margin-top: 1rem;
  letter-spacing: 1.1px;
}
.password {
  display: flex;
  width: 100%;
}
.tv {
  display: flex;
  font-size: 1.6rem;
}
.tv-icon {
  max-width: 1.5rem;
  height: 2rem;
  fill: #fff;
  margin-right: 0.2rem;
}
.ios {
  font-size: 1.6rem;
}

.roku {
  font-size: 1.6rem;
}
.amazon strong {
  font-size: 1.4rem;
  font-weight: 600;
}
.co-partners-footer {
  color: #999;
}

::-ms-reveal {
  filter: invert(100%);
}
.pass-and-showbtn {
  display: flex;
}
.show-hide-btn {
  padding: 0.6rem 1.2rem;
  background-color: #343a40;
  color: white;
  font-weight: 600;
  cursor: pointer;
}
</style>
